.gym-block__description {
  position: relative;

  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;

  width: 84.8%;
  padding-top: 120px;
  padding-bottom: 235px;

  @media (max-width: $tablet-width) {
    flex-direction: column;
    justify-content: center;
    align-items: center;

    width: 88.2%;

    padding-top: 98px;
    padding-bottom: 183px;
  }

  @media (max-width: $mobile-width) {
    width: auto;
    max-width: none;

    margin: 0;
    padding-top: 70px;
    padding-bottom: 277px;
  }
}

.gym-block__room {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  width: 35.5%;

  padding-top: 4.23%;
  margin-right: 7.5%;
  padding-left: 5.2%;

  @media (max-width: $tablet-width) {
    align-items: center;
    width: auto;

    padding: 0 12.32%;
    margin: 0;
  }

  @media (max-width: $mobile-width) {
    align-items: flex-start;

    padding: 0;
  }
}

.gym-block__room h2 {
  @include default-style;
  @include headtitle-desktop;

  margin-bottom: 8px;
  text-align: left;

  white-space: nowrap;

  @media (max-width: $tablet-width) {
    @include headtitle-tablet;

    margin-bottom: 10px;
    text-align: center;
  }

  @media (max-width: $mobile-width) {
    @include headtitle-mobile;
    margin-bottom: 8px;

    text-align: left;
  }
}

.gym-block__room h3 {
  @include default-style;
  margin-bottom: 36px;

  font-family: $font-roboto;
  font-size: 18px;
  line-height: 150%;
  text-align: left;

  color: $darkblue;

  @media (max-width: $tablet-width) {
    margin-bottom: 35px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 23px;

    text-align: left;
  }

}

.gym-block__room p {
  @include default-style;
  margin-bottom: 10px;

  font-family: $font-roboto;
  font-weight: 300;
  font-size: 16px;
  line-height: 150%;
  text-align: left;

  color: $darkblue;

  &:last-child {
    margin-bottom: 0;
  }

  @media (max-width: $tablet-width) {
    font-size: 16px;
    line-height: 150%;

    margin-bottom: 41px;
    text-align: center;

    &:last-child {
      display: none;
    }
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 35px;
    padding-right: 30px;

    text-align: left;
  }
}

.gym-block__img img {
  width: auto;

  @media (max-width: $tablet-width) {
    width: 678px;
  }

  @media (max-width: $mobile-width) {
    min-width: 320px;
    max-height: 313px;

    margin: 0;
    padding: 0;

    overflow-x: hidden;
  }
}

.gym-block__video {
  position: absolute;
  display: block;

  top: 54%;
  left: 38.6%;

  width: 364px;

  &::before {
    content: "";
    position: absolute;
    z-index: 2;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 100px;
    height: 100px;

    background-image: url("../img/icon/video-button.svg");
    background-color: transparent;
    background-repeat: no-repeat;
  }

  @media (max-width: $tablet-width) {
    top: 69%;
    left: 33%;

    width: 266px;

    &::before {
      width: 73px;
      height: 73px;

      background-size: 73px;
    }
  }

  @media (max-width: $mobile-width) {
    position: absolute;

    top: 83%;
    left: 50%;
    transform: translate(-50%, -50%);

    width: 320px;
  }
}
